.form-field {
  box-sizing: border-box;
  display: block;
  width: 100%;
  height: 48px;
  border: 1px solid transparent;
  border-radius: 40px;
  padding: 14px 16px;
  background-color: #f8f8fa;
  
  @include hover(border-color, #d5d7de);

  &::placeholder {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #7e7e83;
    transition: color 1.3s;
  }

  &:hover::placeholder {
    color: $text-color;
  }

  &:focus {
    border-color: $text-color;
    outline: none;

    &::placeholder {
      color: $text-color;
    }

    &:invalid {
      border-color: #f99;
    }
  }

  &--textarea {
    height: 148px;
    font-family: inherit;
    resize: none;
  }
}
